.burger > div {
  width: 30px;
  height: 4px;
  background: #272727;
  border-radius: 4px;
}

.burger > div:before, .burger > div:after {
  content: "";
  width: 30px;
  height: 4px;
  background: #272727;
  border-radius: 4px;
  display: block;
  position: absolute;
}

.burger > div:before {
  margin-top: -10px;
}

.burger > div:after {
  margin-top: 10px;
}

.burger1 {
  transition: all .7s cubic-bezier(.68, -.35, .265, 1.35);
}

.burger1 > div {
  transition: all .1s .3s;
}

.burger1 > div:before, .burger1 > div:after {
  transition: all .3s .2s;
}

.burger1.toggled {
  transform: rotate(180deg);
}

.burger1.toggled > div:before {
  background-color: #ececec;
  transform: rotate(45deg)translate(6px, 8px);
}

.burger1.toggled > div {
  background: none;
}

.burger1.toggled > div:after {
  background-color: #ececec;
  transform: rotate(-45deg)translate(6px, -8px);
}

