nav.navrr {
  z-index: 9;
  height: 100%;
  width: 100%;
  background-color: #272727;
  padding: 100px 50px 60px;
  transition: transform .6s cubic-bezier(.785, .135, .15, .86);
  position: fixed;
  top: 0;
  right: 0;
  overflow-y: auto;
  transform: translateX(100%);
}

nav.navrr.show {
  transform: translateX(0);
}

nav.navrr.show ul.main li {
  opacity: 1;
  transform: translateX(0);
}

nav.navrr.show ul.main li:nth-child(1) {
  transition-delay: .2s;
}

nav.navrr.show ul.main li:nth-child(2) {
  transition-delay: .4s;
}

nav.navrr.show ul.main li:nth-child(3) {
  transition-delay: .6s;
}

nav.navrr.show ul.main li:nth-child(4) {
  transition-delay: .8s;
}

nav.navrr.show ul.main li:nth-child(5) {
  transition-delay: 1s;
}

nav.navrr.show ul.main li:nth-child(6) {
  transition-delay: 1.2s;
}

nav.navrr.show ul.main li:nth-child(7) {
  transition-delay: 1.4s;
}

nav.navrr.show ul.main li:nth-child(8) {
  transition-delay: 1.6s;
}

nav.navrr.show ul.main li:nth-child(9) {
  transition-delay: 1.8s;
}

nav.navrr.show .about, nav.navrr.show .social, nav.navrr.show ul {
  opacity: 1;
  transition-delay: .85s;
  transform: translateY(0);
}

@media (min-width: 667px) {
  nav.navrr {
    padding: 120px 90px 70px;
  }
}

nav.navrr ul.main {
  list-style-type: none;
}

nav.navrr ul.main li {
  opacity: 0;
  margin-bottom: 20px;
  transition: all .3s;
  transform: translateX(40px);
}

nav.navrr ul.main li:last-of-type {
  margin-bottom: 0;
}

nav.navrr ul.main li a {
  color: #c8b392;
  text-transform: uppercase;
  letter-spacing: 5px;
  padding: 10px 0;
  font-size: 1.5rem;
  font-weight: 600;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

nav.navrr ul.main li a span {
  color: #daa520;
}

nav.navrr ul.main li a:hover {
  color: #fff;
}

nav.navrr .about {
  opacity: 0;
